export class OwnSword {
    constructor() {
        this.stats = [
            [1,  28.8,      15,   53],
            [20, 110.88, 57.75,  203],
            [20, 145.44, 75.75,  267],
            [40, 277.92, 144.75, 510],
            [40, 324,    168.75, 594],
            [50, 367.2,  191.25, 673],
            [50, 413.28, 215.25, 758],
            [60, 456.48, 237.75, 837],
            [60, 502.56, 261.75, 921],
            [70, 545.76, 284.25, 1001],
            [70, 591.84, 308.25, 1085],
            [80, 635.04, 330.75, 1164],
        ];
        
        this.name = "I Shall Be My Own Sword";
        this.path = "Destruction";
        this.id = "ownsword";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> Skill DMG </div> 
                <div> by </div> 
                <div className="buff-text">{((0.3 + imposition * 0.05) * 100).toFixed(1)}%.</div>
                <div> When using their Ultimate, immediately </div>
                <div className="buff-text"> regenerates {12 + imposition * 2} Energy </div>
                <div> and increases the wearer's </div>
                <div className="buff-text"> Crit DMG </div>
                <div> by </div>
                <div className="buff-text">{((0.36 + imposition * 0.06) * 100).toFixed(1)}%.</div>
                <div> for 2 turn(s). </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: this.id + "b",
            name: this.name,
            effect:{
                skillBoost: 0.3 + imposition * 0.05,
            },
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: this.id + "0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> When using their Ultimate, immediately </div>
                        <div className="buff-text"> regenerates {12 + imposition * 2} Energy </div>
                        <div> and increases the wearer's </div>
                        <div className="buff-text"> Crit DMG </div>
                        <div> by </div>
                        <div className="buff-text">{((0.36 + imposition * 0.06) * 100).toFixed(1)}%.</div>
                        <div> for 2 turn(s). </div>
                    </div>
                ),
                effect: {
                    critDmg: (0.36 + imposition * 0.06),
                },
                type: "buff",
            },
        }
    }
}

export default OwnSword;